import React, { FC, useCallback, useState, useEffect } from 'react';
import {
  Container,
  makeStyles,
  Box,
  Divider,
  Card,
  CardContent,
  CardHeader,
  Avatar,
  Select,
  MenuItem,
  Grid,
  Switch,
  TextField,
  Breadcrumbs,
  Link
} from '@material-ui/core';

import Page from 'src/components/Page';
import Header from 'src/components/Header';
import { Theme } from 'src/theme';
import StakeContent from './StakeContent';
import UnstakeContent from './UnstakeContent';
import { useStakeState } from 'src/hooks/useStakeState';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%'
    // paddingTop: theme.spacing(3),
    // paddingBottom: theme.spacing(3)
  },

  icon: {
    marginRight: 5,
    display: 'inline-block',
    width: 21,
    height: 21,
    background: '#000000',
    borderRadius: '35px'
  },
  text: {
    marginRight: 15,
    fontSize: 12
  },
  textgroupTitle: {
    fontSize: 12,
    fontWeight: 400,
    color: '#828282',
    lineHeight: '14px',
    marginBottom: 8
  },
  testgroupContent: {
    display: 'flex',
    alignItems: 'center'
  }
}));

type StakeTye = 'stake' | 'unstake';

export const TextGroup = props => {
  const classes = useStyles();

  return (
    <div>
      <div className={classes.textgroupTitle}>{props.title}</div>
      <div className={classes.testgroupContent}>{props.children}</div>
    </div>
  );
};

const StakeView: FC = () => {
  const classes = useStyles();
  const state = useStakeState();
  const { sourceToken, targetToken, account } = state;
  const { t } = useTranslation();

  const [type, setType] = useState<boolean>(true);

  const handleTypeChange = useCallback(e => {
    setType(e.target.checked);
  }, []);

  return (
    <Page className={classes.root} title="Stake">
      <Container maxWidth={false}>
        <Header title={t('mine.title.second')} />
        <Breadcrumbs aria-label="breadcrumb">
          <Link color="inherit" href="/#/mine" onClick={() => {}}>
            {t('menu.mine')}
          </Link>
          <Link color="textPrimary" aria-current="page">
            {t('mine.page.' + (type ? 'stake' :'unstake'))}
          </Link>
        </Breadcrumbs>
        <Box mt={3}></Box>
        <Divider />
        <Card>
          <CardHeader
            title={
              <>
                <Box display="flex" justifyContent="space-between">
                  <Box display="flex" alignItems="center">
                    <TextGroup title={t('mine.staking.stake')}>
                      <Avatar
                        className={classes.icon}
                        src={sourceToken.logoURI}
                      />
                      <span className={classes.text}>{sourceToken.name}</span>
                    </TextGroup>
                    <TextGroup title={t('mine.staking.mines')}>
                      <Avatar
                        className={classes.icon}
                        src={targetToken.logoURI}
                      />
                      <span className={classes.text}>{targetToken.name}</span>
                    </TextGroup>
                  </Box>
                  <Box>
                    {type ? t('mine.staking.stake') : t('mine.staking.unstake')}
                    <Switch
                      defaultChecked
                      color="default"
                      inputProps={{ 'aria-label': t('mine.staking.stake') }}
                      value={type}
                      onChange={handleTypeChange}
                    />
                  </Box>
                </Box>
              </>
            }
          ></CardHeader>
          <CardContent>
            {!account && t('global.loginFirst')}
            {account && type && <StakeContent state={state} />}
            {account && !type && <UnstakeContent state={state} />}
          </CardContent>
        </Card>
      </Container>
    </Page>
  );
};

export default StakeView;
